.aboutcircle:hover{
    transform: rotate(360deg);
    transition-property: transform;
    transition-duration: 1.5s;
}
.aboutinner:hover{
    transform: rotate(-360deg);
    transition-property: transform;
    transition-duration: 1.5s;
}
.skillouter{
    border: 1.5px solid rgba(200, 137, 230, 0.637);
    box-shadow: 4px 5px 4px 3px rgba(89, 4, 168, 0.137);
}
.skillouterlight{
    border: 1.5px solid rgb(47, 46, 56);
    box-shadow: 4px 5px 4px 3px rgb(184, 157, 209);
}
.skillinner{
    background-color: #2b273b;
    background: rgb(142,139,161);
    background: linear-gradient(120deg, rgba(142,139,161,1) 2%, rgba(67,61,101,1) 53%, rgba(48,42,79,1) 94%);
    height: 100%;
    margin: auto;
    align-items: center;
    padding-bottom: 50px;
}
.skillinnerlight{
    background-color: #2b273b;
    background: rgb(133,131,156);
    background: linear-gradient(120deg, rgba(133,131,156,1) 2%, rgba(207,204,226,1) 53%, rgba(140,138,152,1) 94%);
    height: 100%;
    margin: auto;
    align-items: center;
    padding-bottom: 50px;
}
.skillinner:hover{
    background: #2b273b;
    background-image: linear-gradient( 359.4deg,  rgba(12,20,69,1) -16.9%, rgba(71,30,84,1) 119.9% );
    transform: rotate3d(1, 1, 1, 360deg);
    transition-property: transform;
    transition-duration: 20s;
}
.skillinnerlight:hover{
    background: rgb(183,164,210);
    background: linear-gradient(180deg, rgba(183,164,210,1) 17%, rgba(148,125,180,1) 43%, rgba(89,79,103,1) 92%);
    transform: rotate3d(1, 1, 1, 360deg);
    transition-property: transform;
    transition-duration: 20s;
}
.projectouter{
    border: 2.5px solid rgba(200, 137, 230, 0.637);
    padding: 20px;
    box-shadow: 4px 5px 4px 3px rgba(89, 4, 168, 0.137);
}
.projectouterlight{
    border: 2px solid rgb(47, 46, 56);
    box-shadow: 3px 4px 3px 2px rgb(193, 185, 201);
    padding: 15px;
}
.projectouterlight:hover{
    border: none;
    box-shadow: none;
    background: rgb(133,131,156);
    background: linear-gradient(120deg, rgba(133,131,156,1) 2%, rgba(207,204,226,1) 53%, rgba(140,138,152,1) 94%);
    transform: scale(1.02);
    transition-property: transform;
    transition-duration: 1.5s;
}
.projectouter:hover{
    border: 3.5px solid rgba(200, 137, 230, 0.637);
    background: #2b273b;
    background-image: linear-gradient( 359.4deg,  rgba(12,20,69,1) -16.9%, rgba(71,30,84,1) 119.9% );
    transform: scale(1.02);
    transition-property: transform;
    transition-duration: 1.5s;
}

.contact{
    border: 3.5px solid rgba(200, 137, 230, 0.637);
    padding: 20px;
    box-shadow: 4px 5px 4px 3px rgba(89, 4, 168, 0.137);
}
.contactlight{
    border: 2px solid #13022C;
    box-shadow: 3px 4px 3px 2px rgb(193, 185, 201);
    padding: 15px;
}
.contactlight:hover{
    border: 2px solid #13022C;
    box-shadow: none;
    background: rgb(133,131,156);
    background: linear-gradient(120deg, rgba(133,131,156,1) 2%, rgba(207,204,226,1) 53%, rgba(140,138,152,1) 94%);
}
.contact:hover{
    border: 3.5px solid rgba(200, 137, 230, 0.637);
    background: #2b273b;
    background-image: linear-gradient( 359.4deg,  rgba(12,20,69,1) -16.9%, rgba(71,30,84,1) 119.9% );
}
.iconbutton {
    text-decoration: none;
    border-radius: 60px;
    display: inline;
    align-items: center;
    overflow:hidden;
    width: auto;
    max-width: 50px; /** I'm animating max-width because width needs to be auto, and auto can't be animated **/
    -webkit-transition: max-width 0.5s;
    transition: max-width 0.5s;
  }
  
  .iconbutton:hover {
    max-width: 500px;
  }
  
  .icon {
    font-family: "Font Awesome 5 Free";
    font-size: 16px;
    margin-right: 15px;
    padding: 0px 8px;
    display: flex;
    align-items: center;
  }
  
  .text {
    white-space: nowrap;
    padding-right: 15px;
  }